@charset "utf-8";
/* CSS Document */

/* @import "http://fonts.googleapis.com/css?family=Roboto:400,300"; */
html,body {
	border:0;
	margin:0;
	padding:0
}
/* .darkbg {
	background:#6C82A2 url(../assets/img/dark.png) repeat fixed
}
.woodbg {
	background:#E6CCA6 url(../assets/img/wood.png) repeat fixed
} */
.smart-wrap {
	padding:0 20px
}
.smart-forms,.smart-forms * {
	-webkit-box-sizing:border-box;
	-moz-box-sizing:border-box;
	box-sizing:border-box
}
.smart-forms {
	font-family:"Roboto",Arial,Helvetica,sans-serif;
	line-height:1.231;
	font-weight:400;
	font-size:14px;
	color:#34495E
}
.smart-container {
	background:#fff;
	margin:0 auto;
	/*-webkit-box-shadow:0 1px 5px rgba(0,0,0,0.65)
	-moz-box-shadow:0 1px 5px rgba(0,0,0,0.65);
	-o-box-shadow:0 1px 5px rgba(0,0,0,0.65);
	box-shadow:0 1px 5px rgba(0,0,0,0.65);*/
	-webkit-border-radius:5px 5px 0 0;
	-moz-border-radius:5px 5px 0 0;
	-o-border-radius:5px 5px 0 0;
	border-radius:5px 5px 0 0
}
.wrap-0 {
	max-width:952px
}
.wrap-1 {
	max-width:852px
}
.wrap-2 {
	max-width:652px
}
.wrap-3 {
	max-width:452px
}
.smart-forms .section {
	margin-bottom:22px
}
.smart-forms .smart-link {
	color:#1ABC9C;
	text-decoration:none
}
.smart-forms .smart-link:hover {
	text-decoration:underline
}
.smart-forms .tagline {
	height:0;
	border-top:1px solid #D9DDE5;
	text-align:center
}
.smart-forms .tagline span {
	text-transform:uppercase;
	display:inline-block;
	position:relative;
	padding:0 15px;
	background:#fff;
	color:#1ABC9C;
	top:-10px
}
.smart-forms .field {
	display:block;
	position:relative
}
.smart-forms .field-icon i {
	color:#A2A6A8;
	position:relative;
	top:0
}
.smart-forms .field-label {
	display:block;
	margin-bottom:7px
}
.smart-forms .field-label.colm {
	padding-top:12px
}
.smart-forms .field-label em {
	color:#e74c3c;
	font-size:14px;
	font-style:normal;
	display:inline-block;
	margin-left:4px;
	position:relative;
	top:3px
}
.smart-forms .form-header {
	overflow:hidden;
	position:relative;
	padding:9px 30px;
	-webkit-border-radius:2px 2px 0 0;
	-moz-border-radius:2px 2px 0 0;
	-o-border-radius:2px 2px 0 0;
	border-radius:2px 2px 0 0
}
.smart-forms .form-header h4 {
	font-family:"Roboto",Arial,Helvetica,sans-serif;
	text-align:left;
	font-weight:300;
	font-size:22px;
	padding:0;
	margin:0;
	text-align:center;
}
.smart-forms .form-header h4 i {
	font-size:38px;
	position:relative;
	margin-right:10px;
	top:2px
}
.smart-forms .header-primary {
	background-color:#1ABC9C;
	border-top:1px solid #23E0BA;
	border-bottom:4px solid #16A085
}
.smart-forms .header-lite {
	background:#F3F5FA;
	border-top:1px solid #23E0BA;
	border-bottom:1px solid #D9DDE5
}
.smart-forms .header-lite:before {
	content:"";
	background-color:#1ABC9C;
	position:absolute;
	height:8px;
	z-index:1;
	top:0;
	right:0;
	left:0
}
.smart-forms .header-primary h4 {
	color:#fff
}
.smart-forms .header-lite h4 {
	color:#5D6A87;
	padding-top:5px
}
.smart-flat,.smart-flat .form-header {
	-webkit-border-radius:0;
	-moz-border-radius:0;
	-o-border-radius:0;
	border-radius:0
}
.smart-forms .form-body {
	padding:40px 30px;
	padding-bottom:20px
}
.smart-forms .form-footer {
	overflow:hidden;
	padding:7px 14px;
	padding-top:25px;
	/* background:#F3F5FA url(../foobg.png) top left repeat-x */
}
.smart-forms input[type=search] {
	-webkit-appearance:textfield
}
.smart-forms ::-webkit-search-decoration,.smart-forms ::-webkit-search-cancel-button {
	-webkit-appearance:none
}
.smart-forms input::-webkit-input-placeholder,.smart-forms textarea::-webkit-input-placeholder {
	color:#919DA8
}
.smart-forms input:focus::-webkit-input-placeholder,.smart-forms textarea:focus::-webkit-input-placeholder {
	color:#D6DBE0
}
.smart-forms input:-moz-placeholder,.smart-forms textarea:-moz-placeholder {
	color:#919DA8
}
.smart-forms input:focus:-moz-placeholder,.smart-forms textarea:focus:-moz-placeholder {
	color:#D6DBE0
}
.smart-forms input::-moz-placeholder,.smart-forms textarea::-moz-placeholder {
	color:#919DA8;
	opacity:1
}
.smart-forms input:focus::-moz-placeholder,.smart-forms textarea:focus::-moz-placeholder {
	color:#D6DBE0;
	opacity:1
}
.smart-forms input:-ms-input-placeholder,.smart-forms textarea:-ms-input-placeholder {
	color:#919DA8
}
.smart-forms input:focus:-ms-input-placeholder,.smart-forms textarea:focus:-ms-input-placeholder {
	color:#D6DBE0
}
.smart-forms label,.smart-forms input,.smart-forms button,.smart-forms select,.smart-forms textarea {
	margin:0;
	font-size:14px;
	font-family:"Roboto",Arial,Helvetica,sans-serif;
	font-weight:400;
	color:#34495E;
	outline:none
}
.smart-forms .gui-input,.smart-forms .gui-textarea,.smart-forms .select > select,.smart-forms input[type="button"],.smart-forms input[type="submit"],.smart-forms input[type="search"],.smart-forms .select-multiple select {
	-webkit-tap-highlight-color:transparent;
	-webkit-tap-highlight-color:rgba(0,0,0,0);
	-webkit-border-radius:0;
	border-radius:0;
	-webkit-appearance:none;
	-moz-appearance:none;
	appearance:none
}
.smart-forms input[type="search"]::-webkit-search-decoration,.smart-forms input[type="search"]::-webkit-search-cancel-button,.smart-forms input[type="search"]::-webkit-search-results-button,.smart-forms input[type="search"]::-webkit-search-results-decoration {
	display:none
}
.smart-forms .switch,.smart-forms .option,.smart-forms .field-label {
	font-size:14px
}
.smart-forms .radio,.smart-forms .button,.smart-forms .checkbox,.smart-forms .switch > label {
	-webkit-user-select:none;
	-khtml-user-select:none;
	-moz-user-select:none;
	-o-user-select:none;
	user-select:none
}
.smart-forms .radio,.smart-forms .button,.smart-forms .tooltip,.smart-forms .checkbox,.smart-forms .gui-input,.smart-forms .notification,.smart-forms .gui-textarea,.smart-forms .select > select,.smart-forms .select-multiple select,.smart-forms input[type="button"].button,.smart-forms input[type="submit"].button,.smart-forms input[type="search"].gui-input {
	-webkit-transition:all .5s ease-in-out;
	-moz-transition:all .5s ease-in-out;
	-ms-transition:all .5s ease-in-out;
	-o-transition:all .5s ease-in-out;
	transition:all .5s ease-in-out;
	-webkit-border-radius:3px;
	-moz-border-radius:3px;
	-o-border-radius:3px;
	border-radius:3px;
	outline:none
}
.smart-forms .select > select,.smart-forms .select-multiple select {
	-webkit-transition:none;
	-moz-transition:none;
	-ms-transition:none;
	-o-transition:none;
	transition:none
}
.smart-forms .select,.smart-forms .gui-input,.smart-forms .gui-textarea,.smart-forms .select > select,.smart-forms .select-multiple select {
	background:#fff;
	position:relative;
	vertical-align:top;
	border:1px solid #ccc;
	display:-moz-inline-stack;
	display:inline-block;
	display:inline;
	color:#34495E;
	outline:none;
	height:30px;
	width:100%;
	border-radius: 0px !important;
	zoom:1;
	
}
.smart-forms .gui-input,.smart-forms .gui-textarea {
	padding:3px 7px;
}
.smart-forms .gui-textarea {
	resize:none;
	line-height:19px;
	overflow:auto;
	max-width:100%;
	height:96px
}
.smart-forms .input-hint {
	padding:10px;
	display:block;
	margin-top:-4px;
	line-height:16px;
	position:relative;
	background:#F2F4FA;
	border:2px solid #bdc3c7;
	border-top:1px solid #bdc3c7;
	font-family:Arial,Helvetica,sans-serif;
	-webkit-border-radius:0 0 3px 3px;
	-moz-border-radius:0 0 3px 3px;
	-o-border-radius:0 0 3px 3px;
	border-radius:0 0 3px 3px;
	font-size:11px;
	color:#777
}
.smart-forms .select {
	border:0;
	z-index:10
}
.smart-forms .select > select {
	display:block;
	padding:4px 0px;
	-webkit-appearance:none;
	-moz-appearance:none;
	appearance:none;
	outline:none;
	text-indent:.01px;
	z-index:10;
	margin:0
}
.smart-forms .select > select::-ms-expand {
	display:none
}
.smart-forms .select:before,.smart-forms .select:after {
	content:'';
	position:absolute;
	pointer-events:none;
	z-index:15
}
.smart-forms .select:before {
	top:1px;
	right:1px;
	width:34px;
	bottom:1px;
	background:#F2F4FA;
	border-left:1px solid #d3dae7;
	-webkit-border-radius:0 1px 1px 0;
	-moz-border-radius:0 1px 1px 0;
	-o-border-radius:0 1px 1px 0;
	border-radius:0 1px 1px 0
}
.smart-forms .select .arrow {
	position:absolute;
	top:4px;
	right:3px;
	width:24px;
	height:24px;
	background:#F2F4FA;
	color:#B5C1C7;
	pointer-events:none;
	z-index:16
}
.smart-forms .select .arrow:after,.smart-forms .select .arrow:before {
	content:'';
	position:absolute;
	font:12px Consolas,monospace;
	font-style:normal;
	pointer-events:none;
	left:6px
}
.smart-forms .select .arrow:before {
	content:'\25BC';
	bottom:4px
}
.smart-forms .select .double:after {
	content:'\25B2';
	top:-1px
}
.smart-forms .select .double:before {
	content:'\25BC';
	bottom:-1px
}
.smart-forms .select-multiple select {
	width:100%;
	height:123px;
	padding:10px
}
.smart-forms .file {
	display:block;
	width:100%
}
.smart-forms .file .gui-file {
	width:100%;
	height:100%;
	cursor:pointer;
	padding:8px 10px;
	position:absolute;
	-moz-opacity:0;
	opacity:0;
	z-index:11;
	bottom:0;
	right:0
}
.smart-forms .file .button {
	position:absolute;
	top:4px;
	right:4px;
	float:none;
	height:34px;
	line-height:34px;
	padding:0 16px;
	z-index:10
}
.smart-forms .gui-input:hover,.smart-forms .gui-textarea:hover,.smart-forms .select > select:hover,.smart-forms .select-multiple select:hover,.smart-forms .gui-input:hover ~ .input-hint,.smart-forms .file .gui-file:hover + .gui-input,.smart-forms .gui-textarea:hover ~ .input-hint {
	border-color:#24cef7
}
.smart-forms .gui-input:focus,.smart-forms .gui-textarea:focus,.smart-forms .select > select:focus,.smart-forms .select-multiple select:focus {
	border-color:#ccc;
	color:#222;
}
.smart-forms .gui-textarea:focus {
	height:120px
}
.smart-forms .select > select:focus {
	z-index:10;
	z-index:20\9
}
.smart-forms .gui-input:focus ~ .field-icon i,.smart-forms .gui-textarea:focus ~ .field-icon i {
	color:#fff;
}
.smart-forms .select-multiple select:focus,.smart-forms .gui-input:focus ~ .input-hint,.smart-forms .gui-textarea:focus ~ .input-hint,.smart-forms .file .gui-file:focus + .gui-input {
	border-color:#1ABC9C
}
.smart-forms .select > select:focus + .arrow {
	color:#24cef7
}
.smart-forms .option {
	position:relative;
	display:inline-block;
	vertical-align:middle
}
.smart-forms .option > input {
	position:absolute;
	height:inherit;
	width:inherit;
	opacity:0;
	right:0;
}
.smart-forms .checkbox,.smart-forms .radio {
	position:relative;
	margin-right:2px;
	background:#fff;
	display:inline-block;
	border:2px solid #B5C1C7;
	margin-bottom:0;
	margin-top:0;
	height:18px;
	width:18px;
	top:4px
}
.smart-forms .checkbox:before,.smart-forms .radio:before {
	content:'';
	display:none;
	-webkit-box-sizing:content-box;
	-moz-box-sizing:content-box;
	-o-box-sizing:content-box;
	box-sizing:content-box;
	padding:0;
	margin:0
}
.smart-forms input:checked + .checkbox:before,.smart-forms input:checked + .radio:before {
	display:block
}
.smart-forms .checkbox:before {
	position:absolute;
	top:4px;
	left:3px;
	width:6px;
	height:3px;
	border:solid #24cef7;
	border-width:0 0 3px 3px;
	-webkit-transform:rotate(-45deg);
	-moz-transform:rotate(-45deg);
	-ms-transform:rotate(-45deg);
	-o-transform:rotate(-45deg);
	transform:rotate(-45deg)
}
.smart-forms input:checked + .checkbox,.smart-forms input:checked + .radio {
	border:2px solid #084d9a;
	background:#fff;
}
.form-horizontal .checkbox, .form-horizontal .radio {
	min-height: auto;
}
.smart-forms .radio {
	-webkit-border-radius:20px;
	-moz-border-radius:20px;
	-o-border-radius:20px;
	border-radius:20px
}
.smart-forms .radio:before {
	margin:3px auto;
	width:7px;
	height:7px;
	background:#084d9a;
	-webkit-border-radius:10px;
	-moz-border-radius:10px;
	-o-border-radius:10px;
	border-radius:10px
}
.smart-forms input:hover + .checkbox,.smart-forms input:hover + .radio {
	border-color:#084d9a
}
.smart-forms input:focus + .checkbox,.smart-forms input:focus + .radio {
	border-color:#084d9a
}
.smart-forms input:focus + .radio:before {
	background:#084d9a
}
.smart-forms input:focus + .checkbox:before {
	border-color:#084d9a
}
.smart-forms .switch {
	cursor:pointer;
	position:relative;
	padding-right:10px;
	display:inline-block;
	margin-bottom:5px;
	height:26px
}
.smart-forms .switch > .switch-label {
	cursor:pointer;
	display:inline-block;
	position:relative;
	height:25px;
	width:58px;
	color:#fff;
	font-size:10px;
	font-weight:700;
	line-height:20px;
	text-align:center;
	background:#B5C1C7;
	border:2px solid #B5C1C7;
	text-transform:uppercase;
	font-family:Helvetica,Arial,sans-serif;
	-webkit-transition:.3s ease-out;
	-moz-transition:.3s ease-out;
	-o-transition:.3s ease-out;
	transition:.3s ease-out;
	-webkit-border-radius:2px;
	-moz-border-radius:2px;
	-o-border-radius:2px;
	border-radius:2px
}
.smart-forms .switch > .switch-label + span {
	display:inline-block;
	padding-left:5px;
	position:relative;
	top:-7px
}
.smart-forms .switch > .switch-label:before {
	content:attr(data-off);
	position:absolute;
	top:1px;
	right:3px;
	width:33px
}
.smart-forms .switch > .switch-label:after {
	content:"";
	margin:1px;
	width:19px;
	height:19px;
	display:block;
	background:#fff;
	-webkit-border-radius:1px;
	-moz-border-radius:1px;
	-o-border-radius:1px;
	border-radius:1px
}
.smart-forms .switch > input {
	-webkit-appearance:none;
	position:absolute;
	width:inherit;
	height:inherit;
	opacity:0;
	left:0;
	top:0
}
.smart-forms .switch > input:focus {
	outline:none
}
.smart-forms .switch > input:focus + .switch-label {
	color:#fff;
	border-color:#a1a6a9;
	background:#a1a6a9
}
.smart-forms .switch > input:focus + .switch-label:after {
	background:#fff
}
.smart-forms .switch > input:checked + .switch-label {
	border-color:#1ABC9C;
	background:#1ABC9C;
	padding-left:33px;
	color:#fff
}
.smart-forms .switch > input:checked + .switch-label:before {
	content:attr(data-on);
	left:1px;
	top:1px
}
.smart-forms .switch > input:checked + .switch-label:after {
	margin:1px;
	width:19px;
	height:19px;
	background:#fff
}
.smart-forms .switch > input:checked:focus + .switch-label {
	background:#16a085;
	border-color:#16a085
}
.smart-forms .switch-round > .switch-label {
	-webkit-border-radius:13px;
	-moz-border-radius:13px;
	-o-border-radius:13px;
	border-radius:13px
}
.smart-forms .switch-round > .switch-label + span {
	top:-2px
}
.smart-forms .switch-round > .switch-label:before {
	width:33px
}
.smart-forms .switch-round > .switch-label:after {
	width:19px;
	color:#B5C1C7;
	content:"\2022";
	font:20px/20px Times,Serif;
	-webkit-border-radius:13px;
	-moz-border-radius:13px;
	-o-border-radius:13px;
	border-radius:13px
}
.smart-forms .switch-round > input:checked + .switch-label {
	padding-left:33px
}
.smart-forms .switch-round > input:checked + .switch-label:after {
	color:#1ABC9C
}
.smart-forms .button {
	border:0;
	height:33px;
	color:#243140;
	line-height:1;
	font-size:15px;
	cursor:pointer;
	padding:0 18px;
	text-align:center;
	vertical-align:top;
	background:#bdc3c7;
	display:inline-block;
	-webkit-user-drag:none;
	text-shadow:0 1px rgba(255,255,255,0.2)
}
.smart-forms .button:hover {
	color:#243140;
	background:#cacfd2
}
.smart-forms .button:active {
	color:#1d2938;
	background:#a1a6a9
}
.smart-forms a.button,.smart-forms span.button,.smart-forms label.button {
	line-height:42px;
	text-decoration:none
}
.smart-forms .button i {
	font-size:14px
}
.smart-forms .button-list .button {
	margin-bottom:5px
}
.smart-forms .btn-primary {
	background-color:#1abc9c
}
.smart-forms .btn-primary:hover,.smart-forms .btn-primary:focus {
	background-color:#48c9b0
}
.smart-forms .btn-primary:active {
	background-color:#16a085
}
.smart-forms .btn-primary,.smart-forms .btn-primary:hover,.smart-forms .btn-primary:focus,.smart-forms .btn-primary:active {
	color:#fff;
	text-shadow:0 1px rgba(0,0,0,0.08)
}
.smart-forms .btn-rounded {
	-webkit-border-radius:22px;
	-moz-border-radius:22px;
	-o-border-radius:22px;
	border-radius:22px
}
.smart-forms .button-left,.smart-forms .button-right {
	position:relative;
	z-index:9
}
.smart-forms .button-left:before,.smart-forms .button-right:before {
	content:'';
	z-index:-1;
	width:32px;
	height:32px;
	position:absolute;
	background-color:inherit;
	border-color:inherit;
	border:none;
	top:5px
}
.smart-forms .button-left {
	border-left-width:0;
	padding:0 18px 0 7px;
	-webkit-border-radius:0 3px 3px 0;
	-moz-border-radius:0 3px 3px 0;
	-o-border-radius:0 3px 3px 0;
	border-radius:0 3px 3px 0;
	margin-left:20px
}
.smart-forms .button-left:before {
	left:-15px;
	-webkit-border-radius:2px 5px 0 5px;
	-moz-border-radius:2px 5px 0;
	-o-border-radius:2px 5px 0 5px;
	border-radius:2px 5px 0 5px;
	-webkit-transform:rotate(-45deg);
	-moz-transform:rotate(-45deg);
	-ms-transform:rotate(-45deg);
	-o-transform:rotate(-45deg);
	transform:rotate(-45deg)
}
.smart-forms .button-right {
	padding:0 7px 0 18px;
	border-right-width:0;
	-webkit-border-radius:3px 0 0 3px;
	-moz-border-radius:3px 0 0 3px;
	-o-border-radius:3px 0 0 3px;
	border-radius:3px 0 0 3px;
	margin-right:20px
}
.smart-forms .button-right:before {
	right:-15px;
	-webkit-border-radius:5px 2px 5px 0;
	-moz-border-radius:5px 2px 5px 0;
	-o-border-radius:5px 2px 5px 0;
	border-radius:5px 2px 5px 0;
	-webkit-transform:rotate(45deg);
	-moz-transform:rotate(45deg);
	-ms-transform:rotate(45deg);
	-o-transform:rotate(45deg);
	transform:rotate(45deg)
}
.smart-forms .btn-pointed.button-left,.smart-forms .btn-pointed.button-right {
	-webkit-border-radius:22px;
	-moz-border-radius:22px;
	-o-border-radius:22px;
	border-radius:22px
}
.smart-forms .btn-rounded.button-left {
	-webkit-border-radius:0 22px 22px 0;
	-moz-border-radius:0 22px 22px 0;
	-o-border-radius:0 22px 22px 0;
	border-radius:0 22px 22px 0
}
.smart-forms .btn-rounded.button-right {
	-webkit-border-radius:22px 0 0 22px;
	-moz-border-radius:22px 0 0 22px;
	-o-border-radius:22px 0 0 22px;
	border-radius:22px 0 0 22px
}
.smart-forms .pushed {
	-webkit-box-shadow:inset 0 -.3em 0 rgba(0,0,0,0.2);
	-moz-box-shadow:inset 0 -.3em 0 rgba(0,0,0,0.2);
	-o-box-shadow:inset 0 -.3em 0 rgba(0,0,0,0.2);
	box-shadow:inset 0 -.3em 0 rgba(0,0,0,0.2);
	position:relative
}
.smart-forms .pushed:active {
	-webkit-box-shadow:inset 0 -.15em 0 rgba(0,0,0,0.2);
	-moz-box-shadow:inset 0 -.15em 0 rgba(0,0,0,0.2);
	-o-box-shadow:inset 0 -.15em 0 rgba(0,0,0,0.2);
	box-shadow:inset 0 -.15em 0 rgba(0,0,0,0.2);
